import { Component, Inject, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { EmployeeService } from '../../Services/Employee.service';

@Component({
    selector: 'Category',
    template: require('./Category.component.html'),
    styles: [require('../app/app.component.css')]
})

export class CategoryComponent implements OnInit {

    Categoryform: FormGroup;
    errorMessage: any;
    Message: string = '';
    MessageType: string = 'warning';

    CategoryList = [];
    CategoryTable = [];

    ngOnInit() {
        this.MessageType = 'warning';
        this.Message = "Please Wait Loading ...!";

        $(document).ready(function () {
            $(".close").click(function () {
                $(".alert").hide(500);
            });
        });
    }

    constructor(
        http: Http,
        @Inject('BASE_URL') baseUrl: string,
        private _employeeService: EmployeeService,
        private _avRoute: ActivatedRoute,
        private _fb: FormBuilder,
        private _router: Router
    ) {

        this.Categoryform = this._fb.group({
            CategoryId: [''],
            ParentId: [''],
            CategoryName: ['']
        })

        this.fillCategory();

        this.FillForm(0);

    }

    public FillForm(Id: number) {
      
        this._employeeService.GetOneCategory(Id)
            .subscribe((data) => {
                console.log(data);
                this.Categoryform.controls['CategoryId'].setValue(data[0].CategoryId);
                this.Categoryform.controls['ParentId'].setValue(data[0].ParentId);
                this.Categoryform.controls['CategoryName'].setValue(data[0].CategoryName);
            }, error => this.errorMessage = error);

        this.fillCategoryTable();

    }

    public Reset() {
        this.FillForm(0);
        this.Message = " Form clear ...! ";
        this.MessageType = 'success';
        $(document).ready(function () {
            $(".alert").show();
        });
    }

    public Close() {
        this._router.navigate(['/Home']);
    }

    public save() {
        this._employeeService.SaveCategory(this.Categoryform.value)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }


    public Delete(CategoryId: number) {
        this._employeeService.DeleteCategory(CategoryId)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }

    fillCategory() {
        this._employeeService.FillCategory().subscribe((data) => {
            console.log(data);
            this.CategoryList = data;
        });
    }

    fillCategoryTable() {
        this._employeeService.FillCategory().subscribe((data) => {
            console.log(data);
            this.CategoryTable = data;
        });
    }
    
    get CategoryId() { return this.Categoryform.get('CategoryId'); }
    get ParentCategoryId() { return this.Categoryform.get('CityId'); }
    get CategoryName() { return this.Categoryform.get('CityName'); }

}


