import { Component, Inject, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { EmployeeService } from '../../Services/Employee.service';


@Component({
    selector: 'PopularService',
    template: require('./PopularServices.component.html'),
    styles: [require('../app/app.component.css')]
})
export class PopularServiceComponent implements OnInit {

    PopularServiceform: FormGroup;
    errorMessage: any;
    Message: string = '';
    MessageType: string = 'warning';

    CategoryList = [];
    ServiceList = [];

    ngOnInit() {
        this.MessageType = 'warning';
        this.Message = "Please Wait Loading ...!";

        $(document).ready(function () {
            $(".close").click(function () {
                $(".alert").hide(500);
            });
        });
    }

    constructor(
        http: Http,
        @Inject('BASE_URL') baseUrl: string,
        private _employeeService: EmployeeService,
        private _avRoute: ActivatedRoute,
        private _fb: FormBuilder,
        private _router: Router
    ) {

        this.PopularServiceform = this._fb.group({

            ServiceId: [0],
            CategoryId: [0],
            ServiceName: [''],
            Discription: [''],
        })

        this.FillForm(0);
        this.fillCategory();

    }
    public FillForm(Id: number) {
        this._employeeService.GetOnePopularService(Id)
            .subscribe((data) => {
                //console.log(data);
                this.PopularServiceform.controls['ServiceId'].setValue(data[0].ServiceId);
                this.PopularServiceform.controls['CategoryId'].setValue(data[0].CategoryId);
                this.PopularServiceform.controls['ServiceName'].setValue(data[0].ServiceName);
                this.PopularServiceform.controls['Discription'].setValue(data[0].Discription);
            }, error => this.errorMessage = error);
        this.fillService();
    }

    public Reset() {
        this.FillForm(0);
        this.Message = " Form clear ...! ";
        this.MessageType = 'success';
        $(document).ready(function () {
            $(".alert").show();
        });
    }

    public Close() {
        this._router.navigate(['/Home']);
    }

    public save() {
        this._employeeService.SavePopularService(this.PopularServiceform.value)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }


    public Delete(serviceId: number) {

        this._employeeService.DeletePopularService(serviceId)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });

    }

    fillService() {
        this._employeeService.FillService().subscribe((data) => {
            console.log(data);
            this.ServiceList = data;
        });
    }
    fillCategory() {
        this._employeeService.FillCategory().subscribe((data) => {
            this.CategoryList = data;
            console.log(data);
        });
    }

    get ServiceId() { return this.PopularServiceform.get('ServiceId'); }
    get CategoryId() { return this.PopularServiceform.get('CategoryId'); }
    get ServiceName() { return this.PopularServiceform.get('ServiceName'); }
    get Discription() { return this.PopularServiceform.get('Discription'); }
}


