import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';

import { AppComponent } from './components/app/app.component';

import { VendorsComponent } from './components/vendors/Vendors.component';

import { HomeComponent } from './components/home/home.component';

import { StateComponent } from './components/Master/State.component';
import { CityComponent } from './components/Master/City.component';
import { CategoryComponent } from './components/Master/Category.component';

import { PopularServiceComponent } from './components/Master/PopularServices.component';

import { NewRequestComponent } from './components/Requests/NewRequest.component';

//Services
import { EmployeeService } from './Services/Employee.service';
//Employee................!



@NgModule({

    declarations: [
        
        AppComponent,
        HomeComponent,
        StateComponent,
        CityComponent,
        CategoryComponent,
        VendorsComponent,
        PopularServiceComponent,
        NewRequestComponent
        //SystemAdmin -> Masters
        
    ],
    imports: [
        CommonModule,
        HttpModule,
        FormsModule,
        ReactiveFormsModule,
        RouterModule.forRoot([
            { path: '', redirectTo: 'home', pathMatch: 'full' },
            { path: 'home', component: HomeComponent },
            { path: 'Vendors', component: VendorsComponent },
            { path: 'State', component: StateComponent },
            { path: 'City', component: CityComponent },
            { path: 'Category', component: CategoryComponent },
            { path: 'PopularService', component: PopularServiceComponent },
            { path: 'NewRequest', component: NewRequestComponent },
            { path: '**', redirectTo: 'home' }
        ])
    ],
    providers: [EmployeeService]
})
export class AppModuleShared {
}



