import { Component, Inject, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { EmployeeService } from '../../Services/Employee.service';


@Component({
    selector: 'City',
    template: require('./City.component.html'),
    styles: [require('../app/app.component.css')]
})
export class CityComponent implements OnInit {

    Cityform: FormGroup;
    errorMessage: any;
    Message: string = '';
    MessageType: string = 'warning';

    CityList = [];
    StateList = [];

    ngOnInit() {
        this.MessageType = 'warning';
        this.Message = "Please Wait Loading ...!";

        $(document).ready(function () {
            $(".close").click(function () {
                $(".alert").hide(500);
            });
        });
    }

    constructor(
        http: Http,
        @Inject('BASE_URL') baseUrl: string,
        private _employeeService: EmployeeService,
        private _avRoute: ActivatedRoute,
        private _fb: FormBuilder,
        private _router: Router
    ) {

        this.Cityform = this._fb.group({

            CityId: [''],
            //CityCode: [''],
            CityName: [''],
            StateId: [''],
        })

        this.FillForm(0);
        this.fillState();

    }
    public FillForm(Id: number) {
        this._employeeService.GetOneCity(Id)
            .subscribe((data) => {
                console.log(data[0].CityName);
                this.Cityform.controls['CityId'].setValue(data[0].CityId);
                //this.Cityform.controls['CityCode'].setValue(data[0].CityCode);
                this.Cityform.controls['CityName'].setValue(data[0].CityName);
                this.Cityform.controls['StateId'].setValue(data[0].StateId);
            }, error => this.errorMessage = error);
        this.fillCity();
    }

    public Reset() {
        this.FillForm(0);
        this.Message = " Form clear ...! ";
        this.MessageType = 'success';
        $(document).ready(function () {
            $(".alert").show();
        });
    }

    public Close() {
        this._router.navigate(['/Home']);
    }

    public save() {
        this._employeeService.SaveCity(this.Cityform.value)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }


    public Delete(CityId: number) {
        this._employeeService.DeleteCity(CityId)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }

    fillCity() {
        this._employeeService.FillCity().subscribe((data) => {
            console.log(data);
            this.CityList = data;
        });
    }
    fillState() {
        this._employeeService.FillState().subscribe((data) => {
            this.StateList = data;
            console.log(data);
        });
    }

    get CityId() { return this.Cityform.get('CityId'); }
    //get CityCode() { return this.Cityform.get('CityCode'); }
    get CityName() { return this.Cityform.get('CityName'); }
    get StateId() { return this.Cityform.get('StateId'); }
}


