import { Component, Inject, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { EmployeeService } from '../../Services/Employee.service';
import * as $ from 'jquery';


@Component({
    selector: 'State',
    template: require('./vendors.component.html'),
    styles: [require('../app/app.component.css')]
})
export class VendorsComponent implements OnInit {

    Vendorform: FormGroup;
    errorMessage: any;
    Message: string = '';
    MessageType: string = 'warning';

    Checked: any;

    VendorList = [];
    CategoryList = [];
    GenderList = [];
    StateList = [];
    CityList = [];
    SelectedCategoryList = [];

    ngOnInit() {
        this.MessageType = 'warning';
        this.Message = "Please Wait Loading ...!";
        this.Checked = 1;

        $(document).ready(function () {
            $(".close").click(function () {
                $(".alert").hide(500);
            });
            

            $(".addClo").click(function () {
                $(".category_list").hide(500);
            });

            $(".addCat").click(function () {
                $(".category_list").show(500);
            });
        });
    }

    constructor(
        http: Http,
        @Inject('BASE_URL') baseUrl: string,
        private _employeeService: EmployeeService,
        private _avRoute: ActivatedRoute,
        private _fb: FormBuilder,
        private _router: Router
    ) {

        this.Vendorform = this._fb.group({
            VendorId: [0],
            //CategoryId: [''],
            VendorCode: [''],
            VendorName: [''],
            Gender: [0],
            ContactNo: [''],
            Address: [''],
            StateId: [0],
            CityId: [0],
        })

        this.fillState();
        this.fillGender();
        this.fillCategory();
        this.FillForm(0);

    }
    public FillForm(Id: number) {
        console.log("console1")
        this._employeeService.GetOneVendor(Id)
            .subscribe((data) => {
                this.fillVendor();
                this.fillSelectedCategory(data[0].VendorId);
               
                this.Vendorform.controls['VendorId'].setValue(data[0].VendorId);
                if (Id == 0) {
                    this.getNewVendorCode();
                }
                else {
                    this.Vendorform.controls['VendorCode'].setValue(data[0].VendorCode);
                }
                
                this.Vendorform.controls['VendorName'].setValue(data[0].VendorName);
                this.Vendorform.controls['Gender'].setValue(data[0].Gender);
                this.Vendorform.controls['ContactNo'].setValue(data[0].ContactNo);
                this.Vendorform.controls['Address'].setValue(data[0].Address);
                this.Vendorform.controls['StateId'].setValue(data[0].StateId);
                this.fillCity(data[0].StateId);
                this.Vendorform.controls['CityId'].setValue(data[0].CityId);
            }, error => this.errorMessage = error);
        
    }

    public getNewVendorCode() {
        this._employeeService.GetNewVendorCode("Vendor", "VendorCode", "Ven")
            .subscribe((data) => {
                console.log(data);
                this.Vendorform.controls['VendorCode'].setValue(data);
            }, error => this.errorMessage = error);
    }

    public Reset() {
        this.FillForm(0);
        this.Message = " Form clear ...! ";
        this.MessageType = 'success';
        $(document).ready(function () {
            $(".alert").show();
        });
    }

    public Close() {
        this._router.navigate(['/Home']);
    }

    public save() {
        debugger;
        this._employeeService.SaveVendor(this.Vendorform.value)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

            $(document).ready(function () {
                $(".alert").show();
            });
    }

    public SaveCategory(ID: number) {

        this._employeeService.SaveVendorCategory(ID, this.Vendorform.controls['VendorId'].value)
            .subscribe((data) => {
                this.fillSelectedCategory(this.Vendorform.controls['VendorId'].value);
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });


    }


    public Delete(VendorId: number) {
        this._employeeService.DeleteVendor(VendorId)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }
    
    fillVendor() {
        this._employeeService.FillVendor().subscribe((data) => {
            console.log(data);
            this.VendorList = data;
        });
    }

    fillCategory() {
        this._employeeService.FillCategory().subscribe((data) => {
            console.log(data);
            this.CategoryList = data;
        });
    }

    fillSelectedCategory(VendorId: number) {
       
        this._employeeService.FillSelectedCategory(VendorId).subscribe((data) => {
            console.log(data);
            this.SelectedCategoryList = data;
        });
    }

    DeleteSelectedCategory(categoryId: number) {

        this._employeeService.DeleteSelectedCategory(categoryId, this.Vendorform.controls['VendorId'].value)
            .subscribe((data) => {
                this.fillSelectedCategory(this.Vendorform.controls['VendorId'].value);
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                   
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }

    fillGender() {
        this._employeeService.FillGender().subscribe((data) => {
            console.log(data);
            this.GenderList = data;
        });
    }
    
    fillState() {
        this._employeeService.FillState().subscribe((data) => {
            this.StateList = data;
            console.log(data);
        });
    }

    fillCityByState() {

        var ID = this.Vendorform.controls['StateId'].value;
        this.fillCity(ID);
        
    }

    fillCity(ID: number) {

        this._employeeService.FillCityByState(ID).subscribe((data) => {

            if (data[0] != null) {
                this.CityList = data;
            }

        });
    }
    
    get VendorId() { return this.Vendorform.get('VendorId'); }
    get VendorCode() { return this.Vendorform.get('VendorCode'); }
    get VendorName() { return this.Vendorform.get('VendorName'); }
    get Gender() { return this.Vendorform.get('Gender'); }
    get ContactNo() { return this.Vendorform.get('ContactNo'); }
    get Address() { return this.Vendorform.get('Address'); }
    get StateId() { return this.Vendorform.get('StateId'); }
    get CityId() { return this.Vendorform.get('CityId'); }
    
}


