import { Component, Inject, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { EmployeeService } from '../../Services/Employee.service';
import * as $ from 'jquery';


@Component({
    selector: 'State',
    template: require('./State.component.html'),
    styles: [require('../app/app.component.css')]
})
export class StateComponent implements OnInit {

    Stateform: FormGroup;
    errorMessage: any;
    Message: string = '';
    MessageType: string = 'warning';

    StateList = [];
    CountryList = [];

    ngOnInit() {
        this.MessageType = 'warning';
        this.Message = "Please Wait Loading ...!";

        $(document).ready(function () {
            $(".close").click(function () {
                $(".alert").hide(500);
            });
        });
    }

    constructor(
        http: Http,
        @Inject('BASE_URL') baseUrl: string,
        private _employeeService: EmployeeService,
        private _avRoute: ActivatedRoute,
        private _fb: FormBuilder,
        private _router: Router
    ) {

        this.Stateform = this._fb.group({

            StateId: [''],
            StateName: [''],
            CountryId: [''],
        })

        this.FillForm(0);
        this.fillCountry();

    }
    public FillForm(Id: number) {
        console.log("console1")
        this._employeeService.GetOneState(Id)
            .subscribe((data) => {
                console.log(data[0].StateName);
                this.Stateform.controls['StateId'].setValue(data[0].StateId);
                this.Stateform.controls['StateName'].setValue(data[0].StateName);
                this.Stateform.controls['CountryId'].setValue(data[0].CountryId);
            }, error => this.errorMessage = error);
        this.fillState();
    }

    public Reset() {
        this.FillForm(0);
        this.Message = " Form clear ...! ";
        this.MessageType = 'success';
        $(document).ready(function () {
            $(".alert").show();
        });
    }

    public Close() {
        this._router.navigate(['/Home']);
    }

    public save() {
        this._employeeService.SaveState(this.Stateform.value)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }


    public Delete(StateId: number) {
        this._employeeService.DeleteState(StateId)
            .subscribe((data) => {
                this.Message = data[0].Message;
                if (data[0].MessageType == 1) {
                    this.FillForm(0);
                    this.MessageType = 'success';
                }
                else {
                    this.MessageType = 'warning';
                }
            }, error => this.errorMessage = error);

        $(document).ready(function () {
            $(".alert").show();
        });
    }
    
    fillState() {
        this._employeeService.FillState().subscribe((data) => {
            console.log(data);
            this.StateList = data;
        });
    }
    fillCountry() {
        this._employeeService.FillCountry().subscribe((data) => {
            this.CountryList = data;
            console.log(data);
        });
    }

    get StateId() { return this.Stateform.get('StateId'); }
    get StateName() { return this.Stateform.get('StateName'); }
    get CountryId() { return this.Stateform.get('CountryId'); }
}


